<template>
  <div>
    <el-row :gutter="20">
      <el-col
        v-for="item in templates"
        :key="item.id"
        class="mb20"
        :xl="6"
        :lg="6"
        :md="8"
        :sm="24"
        :xs="24"
      >
        <el-card>
          <div slot="header">
            <span style="font-weight: bolder;">{{item.name}}</span>
          </div>
          <div>
            <el-image style="height: 230px" :src="item.img" fit="scale-down"></el-image>
          </div>
          <div class="setting-bar">
            <el-button
              v-if="item.active == 1"
              type="primary"
              size="small"
              icon="el-icon-unlock"
              circle
            ></el-button>
            <el-button
              v-else
              size="small"
              icon="el-icon-lock"
              circle
              @click="setActive(item.id,item.name)"
            ></el-button>
            <el-dropdown>
              <span class="el-dropdown-link" style="cursor: pointer;">
                <i class="el-icon-more" style="margin-right:10px;"></i>更多
              </span>
              <el-dropdown-menu slot="dropdown" @command="handleCommand">
                <el-dropdown-item v-if="item.active == 0" :command="{name:'delete',id:item.id}">删除</el-dropdown-item>
                <el-dropdown-item :command="{name:'updateOline',id:item.id}">在线更新</el-dropdown-item>
                <el-dropdown-item :command="{name:'updateLocal',id:item.id}">本地更新</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </el-card>
      </el-col>
      <el-col class="mb20" :xl="6" :lg="6" :md="8" :sm="24" :xs="24">
        <div style="text-align: center;line-height: 397px;">
          <el-button type="primary" style="font-size: 30px;" icon="el-icon-plus" circle></el-button>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import templateApi from "@/api/templateApi";
export default {
  components: {},
  data() {
    return {
      activeTemplate: {},
      templates: []
    };
  },
  created() {
    this.getTemplates();
  },
  methods: {
    getTemplates() {
      templateApi.findAll().then(res => {
        this.templates = res.data;
      });
    },
    setActive(id, name) {
      templateApi.setActive({ id: id, name: name }).then(res => {
        this.$message.success("设置成功");
        this.getTemplates();
      });
    },
    handleCommand(command) {
      console.log(command);
      switch (command.name) {
        case "delete":
          templateApi.delete(command.id).then(res => {
            this.$message.success("删除成功");
            this.getTemplates();
          });
          break;

        default:
          break;
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.setting-bar {
  padding-top: 15px;
  margin-top: 10px;
  border-top: 1px solid #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
</style>