import request from '@/utils/request'

const baseUrl = '/api/admin/template'

const templateApi = {}

templateApi.findAll = (params) => {
  return request({
    url: `${baseUrl}`,
    params: params,
    method: 'get'
  })
}

templateApi.directory = (params) => {
  return request({
    url: `${baseUrl}/directory`,
    params: params,
    method: 'get'
  })
}

templateApi.fileList = () => {
  return request({
    url: `${baseUrl}/file-list`,
    method: 'get'
  })
}

templateApi.getContent = (params) => {
  return request({
    url: `${baseUrl}/content`,
    params: params,
    method: 'get'
  })
}

templateApi.write = file => {
  return request({
    url: `${baseUrl}/write`,
    data: file,
    method: 'put'
  })
}

templateApi.copyFile = (data) => {
  return request({
    url: `${baseUrl}/file/copy`,
    data: data,
    method: 'post'
  })
}

templateApi.createDir = (data) => {
  return request({
    url: `${baseUrl}/directory/create`,
    data: data,
    method: 'post'
  })
}

templateApi.createFile = (data) => {
  return request({
    url: `${baseUrl}/create`,
    data: data,
    method: 'post'
  })
}

templateApi.rename = (src, target) => {
  return request({
    url: `${baseUrl}/rename`,
    params: { srcPath: src, targetPath: target },
    method: 'put'
  })
}

templateApi.deleteFile = param => {
  return request({
    url: `${baseUrl}/file/delete`,
    data: param,
    method: 'delete'
  })
}

templateApi.update = (params) => {
  return request({
    url: `${baseUrl}`,
    data: params,
    method: 'put'
  })
}

templateApi.setActive = (params) => {
  return request({
    url: `${baseUrl}/setActive`,
    data: params,
    method: 'put'
  })
}

templateApi.delete = templateId => {
  return request({
    url: `${baseUrl}/${templateId}`,
    method: 'delete'
  })
}

export default templateApi
